<script>
export default {
  name: "App",
  data() {
    return {
      searchKey: "",
      showEnterSearch: false,
      showInpSearch: false,
      /* 显示搜索图标 */
      showSearch: false,
      /* 进度 */
      loadData: 10,
      progress: {
        width: 0
      },
      /* 进度 */
      mapData: [],
      currentData: [],
      travelData: [],
      foodData: [],
      hotelData: [],
      shoppingData: [],
      menu: 0,
      //   正在切换目录
      changeFlag: false,
      showTip: false,
      /* 正在加载 */
      loadingFlag: false,
      /* 开始按钮切换 */
      start: true,
      /* 地图 */
      map: false
    };
  },
  created() {
    document.onreadystatechange = this.completeLoading;
    this.getMapInfo();
  },
  methods: {
    filterBy(data, key) {
      return data.filter(data => {
        if (data.region == key) {
          return data.region;
        } else {
          return data.name.match(key);
        }
      });
    },
    checkEnterSearch(n) {
      if (n === 0) {
        this.showInpSearch = !this.showInpSearch;
        this.showEnterSearch = false;
        return;
      } else if (n === 1) {
        this.showEnterSearch = true;
      }
    },
    enterSearch() {
      this.showInpSearch = !this.showInpSearch;
    },
    // 介绍信息
    showIntro(n) {
      this.checkEnterSearch(0);
      this.mapData.forEach((item, i) => {
        if (item.name == n) {
          this.mapData[i].introFlag = 1;
        }
      });
    },
    // 获取地图信息
    getMapInfo() {
      this.$axios.get("travel.json").then(res => {
        this.travelData = res.data.info;
        this.mapData.push(...this.travelData);
      });
      this.$axios.get("food.json").then(res => {
        this.foodData = res.data.info;
        this.mapData.push(...this.foodData);
      });
      this.$axios.get("hotel.json").then(res => {
        this.hotelData = res.data.info;
        this.mapData.push(...this.hotelData);
      });
      this.$axios.get("shopping.json").then(res => {
        this.shoppingData = res.data.info;
        this.mapData.push(...this.shoppingData);
      });

      console.log(this.mapData);
    },
    //   切换目录
    changeMenu(n) {
      if (this.changeFlag) {
        return;
      }
      this.changeFlag = true;
      this.menu = 0;
      if (n === 1) {
        this.currentData = this.travelData;
      } else if (n === 2) {
        this.currentData = this.foodData;
      } else if (n === 3) {
        this.currentData = this.hotelData;
      } else if (n === 4) {
        this.currentData = this.shoppingData;
      }
      setTimeout(() => {
        this.menu = n;
      }, 500);
      setTimeout(() => {
        this.changeFlag = false;
      }, 1000);
    },
    //   隐藏介绍信息
    hiddenIntr() {
      this.showTip = false;
      setTimeout(() => {
        this.showSearch = true;
      }, 500);
      // console.log("111");
    },
    //   点击开始，进入地图
    enterMap() {
      this.start = false;
      setTimeout(() => {
        this.map = true;
        this.menu = 1;
        this.currentData = this.travelData;
      }, 500);

      setTimeout(() => {
        this.showTip = true;
      }, 1000);
    },
    // 加载判断
    completeLoading() {
      if (document.readyState == "complete") {
        this.loadingFlag = false;
      }
    }
  }
};
</script>

