.intro-enter,
.intro-leave-to {
  opacity: 0;
}
.intro-leave,
.intro-enter-to {
  opacity: 1;
}
.intro-enter-active,
.intro-leave-active {
  transition: 0.5s;
}


.enterSearch-enter-to {
  transform-origin: right center;
  transform: scale(1);
}
.enterSearch-enter {
  transform-origin: right center;
  transform: scale(0);
}
.enterSearch-leave {
  opacity: 1;
}
.enterSearch-leave-to {
  opacity: 0;
}
.enterSearch-enter-active,
.enterSearch-leave-active {
  transition: 0.3s;
}

::-webkit-scrollbar {
  width: 5px;
}

/*定义滑块颜色、内阴影及圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(249, 150, 29);
}
/*定义滚动条的轨道颜色、内阴影及圆角*/
::-webkit-scrollbar-track {
  background-color: rgb(171, 125, 72);
  border-radius: 10px;
}
</style>



